define('my-app/controllers/contacts/index', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var ContactsIndexController = Ember['default'].Controller.extend({
    contactName: 'Anostagia',
    avatar: 'images/avatar.png',
    open: (function () {
      return new Date().getDay() === 0 ? 'Closed' : 'Open';
    }).property()
  });

  exports['default'] = ContactsIndexController;

});