define('my-app/components/product/details', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var ProductDetailsComponent = Ember['default'].Component.extend({
    reviewsCount: Ember['default'].computed.alias('product.reviews.length'),
    hasReviews: (function () {
      return this.get('reviewsCount') > 0;
    }).property('reviewsCount')
  });

  exports['default'] = ProductDetailsComponent;

});