define('my-app/routes/products/deals', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var ProductsDealsRoute = Ember['default'].Route.extend({
    model: function model() {
      return this.modelFor('products').filter(function (product) {
        return product.get('price') < 500;
      });
    }
  });

  exports['default'] = ProductsDealsRoute;

});