define('my-app/routes/products/onsale', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var ProductsOnsaleRoute = Ember['default'].Route.extend({
    model: function model() {
      return this.modelFor('products').filterBy('isOnSale');
    }
  });

  exports['default'] = ProductsOnsaleRoute;

});