define('my-app/models/review', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var Review = DS['default'].Model.extend({
    text: DS['default'].attr('string'),
    reviewedAt: DS['default'].attr('date'),
    product: DS['default'].belongsTo('product')
  });

  Review.reopenClass({
    FIXTURES: [{
      id: 100,
      text: 'Started a fire in no time!'
    }, {
      id: 101,
      text: 'Not the brightest flame, but warm!'
    }] });

  exports['default'] = Review;

});