define('my-app/components/contact/details', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var ContactDetailsComponent = Ember['default'].Component.extend({
    productsCount: Ember['default'].computed.alias('contact.products.length'),
    isProductive: (function () {
      return this.get('productsCount') > 3;
    }).property('productsCount')
  });

  exports['default'] = ContactDetailsComponent;

});