define('my-app/controllers/product', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var ProductController = Ember['default'].Controller.extend({
    text: '',
    actions: {
      createReview: function createReview() {
        var review = this.store.createRecord('review', {
          text: this.get('text'),
          product: this.get('model'),
          reviewedAt: new Date()
        });
        var controller = this;
        review.save().then(function () {
          controller.set('text', '');
          controller.get('model.reviews').addObject(review);
        });
      }
    }
  });

  exports['default'] = ProductController;

});