define('my-app/models/contact', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var Contact = DS['default'].Model.extend({
    name: DS['default'].attr('string'),
    about: DS['default'].attr('string'),
    avatar: DS['default'].attr('string'),
    products: DS['default'].hasMany('product', { async: true })
  });
  Contact.reopenClass({
    FIXTURES: [{
      id: 200,
      name: 'Giamia',
      about: 'Although Giamia came from a humble spark of lightning, he quickly grew to be a great craftsman, providing all the warming instruments needed by those close to him.',
      avatar: 'images/contacts/giamia.png',
      products: [1, 4]
    }, {
      id: 201,
      name: 'Anostagia',
      about: 'Knowing there was a need for it, Anostagia drew on her experience and spearheaded the Flint & Flame storefront. In addition to coding the site, she also creates a few products available in the store.',
      avatar: 'images/contacts/anostagia.png',
      products: [2, 3, 5, 6]
    }] });

  exports['default'] = Contact;

});